import { createRouter, createWebHistory } from 'vue-router';
import ArticleDetail from '@/components/ArticleDetail.vue'; // 正しいパスに注意してください

const routes = [
  {
    path: '/article/:id',
    name: 'ArticleDetail',
    component: ArticleDetail,
    props: true // ルートパラメータをプロップスとしてコンポーネントに渡す
  }
  // 他のルート定義
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
