<template>
  <div class="article-detail" v-if="article">
    <h1>{{ article.title }}</h1>
    <img :src="article.image" alt="Article Image">
    <!-- 記事の内容やその他のデータを表示 -->
  </div>
  <div v-else>
    <p>記事が見つかりません。</p>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  computed: {
    ...mapState(['articles']),
    article() {
      const articleId = this.$route.params.id;
      return this.articles.find(article => article.id === parseInt(articleId, 10));
    }
  }
};
</script>

